<template>
  <div class="w3-container w3-padding-24 w3-center" v-if="topic">
    <h2>{{ topicName }} Details</h2>
    <br>

    <table class="w3-table-all w3-content"
      style="max-width:500px;">
      <tr>
        <th>Topic ID:</th>
        <td>{{ topic.id }}</td>
      </tr>
      <tr>
        <th>Name:</th>
        <td>{{ topic.name }}</td>
      </tr>
      <tr>
        <th>Description:</th>
        <td>{{ topic.description }}</td>
      </tr>
    </table>
    <br>

    <template v-if="isAdmin">
      <button
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        style="min-width:150px;"
        @click="$router.push(`/topics/${topic.id}/update-topic`)">
        Update Topic
      </button>
      <button
        class="w3-button w3-border w3-border-red w3-hover-red w3-hover-text-white"
        style="min-width:150px;"
        @click="deleteTopic(topic.id)">
        Delete Topic
      </button>
    </template>
    <hr>

    <h3>{{ topicName }} Courses</h3>
    <ul class="w3-ul w3-content w3-border w3-border-light-blue w3-hover-shadow"
      style="max-width:500px;"
      v-if="courses.length">
      <li class="w3-border-light-blue w3-hover-light-blue w3-hover-text-white"
        v-for="course in courses" :key="course.id"
        @click="$router.push(`/topics/${course.topicId}/courses/${course.id}`)">
        {{ course.name }}
      </li>
    </ul>
    <p v-else>Sorry, there are currently no courses for this topic</p>
    <br>

    <template v-if="isAdmin">
      <button class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        @click="$router.push(`/topics/${topic.id}/courses/create-course`)">
        Create New Course
      </button>
    </template>

  </div>
  <div class="w3-container w3-padding-24 w3-center" v-else>
    <h3>Sorry, there is no topic with ID: {{$route.params.topicId}}</h3>
    <a @click="$router.push('/topics')">Return to Topic Listing</a>
  </div>
</template>

<script>
import UserStore from '@/stores/UserStore.js'
import TopicStore from '@/stores/TopicStore.js'
import CourseStore from '@/stores/CourseStore.js'

export default {
  data() {
    return {
      topic: null,// TopicStore.data.currentTopic,
      courses: [],// CourseStore.data.courses
    };
  },
  created() {
    const topicId = this.$route.params.topicId;

    TopicStore.methods.getTopicById(topicId).then(value => {
      this.topic = value;
    });
    CourseStore.methods.getCoursesByTopicId(topicId).then(value => {
      this.courses = value;
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   const p1 = TopicStore.methods.getTopicById(to.params.topicId);
  //   const p2 = CourseStore.methods.getCoursesByTopicId(to.params.topicId);
  //   Promise.all([p1, p2]).then(() => next());
  // },
  computed: {
    isAdmin() {
      return UserStore.data.roles.includes('ROLE_ADMIN');
    },
    topicName() {
      return this.topic ? this.topic.name : 'Topic';
    }
  },
  methods: {
    deleteTopic(topicId) {
      TopicStore.methods.deleteTopic(topicId)
        .then(() => this.$router.push('/topics'));
    }
  }
}
</script>
