<template>
  <div class="w3-container w3-padding-24 w3-center">
    <h2>Create a New Course</h2>
    <br>

    <div v-if="errors.length"
      class="w3-panel w3-content w3-card w3-pale-yellow"
      style="max-width:500px;">
      <p><b>Error:</b></p>
      <ul class="w3-ul">
        <li v-for="(error, index) in errors" :key="index">{{ error }}</li>
      </ul>
    </div>

    <form @submit.prevent="createCourse(name, description, topicId)"
      class="w3-content" style="max-width:300px;">

      <label>Course Name</label>
      <input autofocus
        class="w3-input w3-border"
        v-model="name"/>
      <br>

      <label>Course Description</label>
      <textarea class="w3-input w3-border"
        v-model="description"/>
      <br>

      <label>Topic</label>
      <select class="w3-select w3-border w3-white"
        v-model.number="topicId">
        <option v-for="topic in topics" :key="topic.id" :value="topic.id">
          {{ topic.name }}
        </option>
      </select>
      <br><br>

      <button onclick="submit"
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        style="min-width:100px;">
        Submit
      </button>
      <button
        class="w3-button w3-border w3-border-red w3-hover-red w3-hover-text-white"
        style="min-width:100px;"
        @click.prevent="resetForm()">
        Reset
      </button>
    </form>
    <br><br>

    <a @click="$router.push(`/topics/${$route.params.topicId}`)">Return to Topic Details</a>
  </div>
</template>

<script>
import CourseStore from '@/stores/CourseStore.js'
import TopicStore from '@/stores/TopicStore.js'

export default {
  data() {
    return {
      name: null,
      description: null,
      topicId: parseInt(this.$route.params.topicId),
      topics: [],// TopicStore.data.topics
      errors: []
    };
  },
  created() {
    TopicStore.methods.getAllTopics().then(value => {
      this.topics = value;
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   TopicStore.methods.getAllTopics().then(() => next());
  // },
  methods: {
    createCourse(name, description, topicId) {
      this.errors = CourseStore.methods.validateCourse(name, description, topicId);

      if (!this.errors.length) {
        CourseStore.methods.createCourse(name, description, topicId)
          .then(() => this.$router.push(`/topics/${topicId}`),
            err => this.errors.push(err));
      }
    },
    resetForm() {
      this.name = this.description = null;
      this.topicId = parseInt(this.$route.params.topicId);
      this.errors = [];
    }
  }
}

</script>
