<template>
  <div class="w3-container w3-padding-24 w3-center">
    <h2>Available Topics</h2>
    <br>

    <ul class="w3-ul w3-content w3-border w3-border-light-blue w3-hover-shadow"
      style="max-width:500px;"
      v-if="topics.length">
      <li class="w3-border-light-blue w3-hover-light-blue w3-hover-text-white"
        v-for="topic in topics" :key="topic.id"
        @click="$router.push(`/topics/${topic.id}`)">
        {{ topic.name }}
      </li>
    </ul>
    <h5 v-else>Sorry, there are currently no topics available</h5>
    <br>

    <template v-if="isAdmin">
      <button @click="$router.push('/topics/create-topic')"
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white">
        Create New Topic
      </button>
    </template>
  </div>
</template>

<script>
import UserStore from '@/stores/UserStore.js'
import TopicStore from '@/stores/TopicStore.js'

export default {
  data() {
    return {
      topics: []// TopicStore.data.topics
    };
  },
  created() {
    TopicStore.methods.getAllTopics().then(value => {
      this.topics = value;
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   TopicStore.methods.getAllTopics().then(() => next());
  // },
  computed: {
    isAdmin() {
      return UserStore.data.roles.includes('ROLE_ADMIN');
    }
  }
}
</script>
