import jQuery from 'jquery'
import UserStore from './UserStore.js'

const TopicStore = {
  data: {
    currentTopic: null,
    topics: []
  },
  methods: {
    getAllTopics() {
      return jQuery.ajax(`http://localhost:8080/api/topics`, {
        dataType: 'json',
        headers: { 'Authorization': UserStore.data.accessToken }
      }).done(data => {
        TopicStore.data.topics = data;
      });
    },
    getTopicById(topicId) {
      return jQuery.ajax(`http://localhost:8080/api/topics/${topicId}`, {
        dataType: 'json',
        headers: { Authorization: UserStore.data.accessToken }
      }).done(data => {
        TopicStore.data.currentTopic = data;
      });
    },
    createTopic(name, description) {
      return jQuery.ajax(`http://localhost:8080/api/topics`, {
        contentType: 'application/json',
        data: JSON.stringify({ name, description }),
        headers: { Authorization: UserStore.data.accessToken },
        type: 'post'
      });
    },
    updateTopic(topicId, name, description) {
      return jQuery.ajax(`http://localhost:8080/api/topics/${topicId}`, {
        contentType: 'application/json',
        data: JSON.stringify({ name, description }),
        headers: { Authorization: UserStore.data.accessToken },
        type: 'put'
      }).done(data => {
        TopicStore.data.currentTopic = data;
      });
    },
    deleteTopic(topicId) {
      return jQuery.ajax(`http://localhost:8080/api/topics/${topicId}`, {
        headers: { Authorization: UserStore.data.accessToken },
        type: 'delete'
      });
    },
    validateTopic(name, description) {
      let errors = [];

      if (!name) {
        errors.push('Topic name required');
      } else if (name.length > 25) {
        errors.push('Topic name cannot exceed 25 characters');
      } else if (!/\S/.test(name)) {
        errors.push('Topic name must contain at least 1 non-whitespace character');
      }

      if (!description) {
        errors.push('Topic description required');
      }	else if (description.length > 100) {
        errors.push('Topic description cannot exceed 100 characters');
      }	else if (!/\S/.test(description)) {
        errors.push('Topic description must contain at least 1 non-whitespace character');
      }
      return errors;
    }
  }
};

export default TopicStore;
