import Vue from 'vue'
import App from './App.vue'
import router from './router'
import UserStore from './stores/UserStore.js'

Vue.config.productionTip = false

// get these before the Vue instance is created (ie, for page reloading)
UserStore.data.roles = sessionStorage.getItem('roles');
UserStore.data.accessToken = sessionStorage.getItem('accessToken');

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
