<template>
  <div class="w3-container w3-padding-24 w3-center">
    <h3>Update Topic</h3>
    <br>

    <div v-if="errors.length"
      class="w3-panel w3-content w3-card w3-pale-yellow"
      style="max-width:500px;">
      <p><b>Error:</b></p>
      <ul class="w3-ul">
        <!-- not sure if this is right way -->
        <li v-for="(error, index) in errors" :key="index">{{ error }}</li>
      </ul>
    </div>

    <form @submit.prevent="updateTopic($route.params.topicId, name, description)"
      class="w3-content" style="max-width:300px;">

      <label>Topic Name</label>
      <input autofocus
        class="w3-input w3-border"
        v-model="name">
      <br>

      <label>Topic Description</label>
      <textarea class="w3-input w3-border"
        v-model="description"/>
      <br>

      <button onclick="submit"
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        style="min-width:100px;">
        Submit
      </button>
      <button
        class="w3-button w3-border w3-border-red w3-hover-red w3-hover-text-white"
        style="min-width:100px;"
        @click.prevent="resetForm()">
        Reset
      </button>
    </form>
    <br><br>

    <a @click="$router.push(`/topics/${$route.params.topicId}`)">Return to Topic Details</a>
  </div>
</template>

<script>
import TopicStore from '@/stores/TopicStore.js'

export default {
  data() {
    return {
      name: null,
      description: null,
      errors: []
    };
  },
  created() {
    TopicStore.methods.getTopicById(this.$route.params.topicId).then(value => {
      const topic = JSON.parse(JSON.stringify(value));
      this.name = topic.name;
      this.description = topic.description;
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   TopicStore.methods.getTopicById(to.params.topicId).then(() => next());
  // },
  methods: {
    updateTopic(topicId, name, description) {
      this.errors = TopicStore.methods.validateTopic(name, description);

      if (!this.errors.length) {
        TopicStore.methods.updateTopic(topicId, name, description)
          .then(() => this.$router.push(`/topics/${topicId}`),
            err => this.errors.push(err));
      }
    },
    resetForm() {
      const topic = JSON.parse(JSON.stringify(TopicStore.data.currentTopic));
      this.name = topic.name;
      this.description = topic.description;
      this.errors = [];
    }
  }
}
</script>
