<template>
  <div class="w3-container w3-padding-24 w3-center">
    <h3>Please Sign In</h3>
    <br>

    <div v-if="errors.length"
      class="w3-panel w3-content w3-card w3-pale-yellow"
      style="max-width:500px;">
      <p><b>Error:</b></p>
      <ul class="w3-ul">
        <li v-for="(error, index) in errors" :key="index">{{ error }}</li>
      </ul>
    </div>

    <form @submit.prevent="signIn(username, password)"
      class="w3-content" style="max-width:300px">

      <label>Username</label>
      <input v-model="username" autofocus
        class="w3-input w3-border"/>
      <br>

      <label>Password</label>
      <input type="password" v-model="password"
        class="w3-input w3-border"/>
      <br>

      <button onclick="submit"
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        style="min-width:100px;">
        Submit
      </button>
      <button @click.prevent="resetForm()"
        class="w3-button w3-border w3-border-red w3-hover-red w3-hover-text-white"
        style="min-width:100px;">
        Reset
      </button>
    </form>
  </div>
</template>

<script>
import UserStore from '@/stores/UserStore.js'

export default {
  data() {
    return {
      username: null,
      password: null,
      errors: []
    };
  },
  methods: {
    signIn(username, password) {
      this.errors = [];

      UserStore.methods.signIn(username, password)
        .then(() => this.$router.push('/'),
          () => this.errors.push('Invalid username and/or password'));
    },
    resetForm() {
      this.username = this.password = null;
      this.errors = [];
    }
  }
}
</script>
