<template>
  <div class="w3-container w3-padding-24 w3-center" v-if="course">
    <h2>{{ courseName }} Details</h2>
    <br>

    <table class="w3-table-all w3-content"
      style="max-width:500px;">
      <tr>
        <th>Course ID:</th>
        <td>{{ course.id }}</td>
      </tr>
      <tr>
        <th>Name:</th>
        <td>{{ course.name }}</td>
      </tr>
      <tr>
        <th>Description:</th>
        <td>{{ course.description }}</td>
      </tr>
      <tr>
        <th>Topic:</th>
        <td>{{ topicName }}</td>
      </tr>
    </table>
    <br>

    <template v-if="isAdmin">
      <button
        class="w3-button w3-border w3-border-orange w3-hover-orange w3-hover-text-white"
        style="min-width:150px;"
        @click="$router.push(`/topics/${topic.id}/courses/${course.id}/update-course`)">
        Update Course
      </button>
      <button
        class="w3-button w3-border w3-border-red w3-hover-red w3-hover-text-white"
        style="min-width:150px;"
        @click="deleteCourse(course.id)">
        Delete Course
      </button>
    </template>
    <br><br><br>

    <a @click="$router.push(`/topics/${$route.params.topicId}`)">
      Return to {{ topicName }} Details
    </a>
  </div>
  <div class="w3-container w3-padding-24 w3-center" v-else>
    <h3>Sorry, there is no course with ID: {{ $route.params.courseId }}</h3>
    <a @click="$router.push(`/topics/${$route.params.topicId}`)">Return to {{ topicName }} Details</a>
  </div>
</template>

<script>
import CourseStore from '@/stores/CourseStore.js'
import TopicStore from '@/stores/TopicStore.js'
import UserStore from '@/stores/UserStore.js'

export default {
  data() {
    return {
      course: null,// CourseStore.data.currentCourse,
      topic: null// TopicStore.data.currentTopic
    };
  },
  created() {
    const params = this.$route.params;

    TopicStore.methods.getTopicById(params.topicId).then(value => {
      this.topic = value;
    });
    CourseStore.methods.getCourseById(params.courseId).then(value => {
      this.course = value;
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   const p1 = TopicStore.methods.getTopicById(to.params.topicId);
  //   const p2 = CourseStore.methods.getCourseById(to.params.courseId);
  //   Promise.all([p1, p2]).then(() => next());
  // },
  computed: {
    isAdmin() {
      return UserStore.data.roles.includes('ROLE_ADMIN');
    },
    topicName() {
      return this.topic ? this.topic.name : 'Topic';
    },
    courseName() {
      return this.course ? this.course.name : 'Course';
    }
  },
  methods: {
    deleteCourse(courseId) {
      CourseStore.methods.deleteCourse(courseId)
        .then(() => this.$router.push(`/topics/${this.topic.id}`));
    }
  }
}
</script>
