import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'
import SignUp from './components/SignUp.vue'
import SignIn from './components/SignIn.vue'
import Topics from './components/Topics.vue'
import TopicDetail from './components/TopicDetail.vue'
import CreateTopic from './components/CreateTopic.vue'
import UpdateTopic from './components/UpdateTopic.vue'
import CreateCourse from './components/CreateCourse.vue'
import CourseDetail from './components/CourseDetail.vue'
import UpdateCourse from './components/UpdateCourse.vue'
import UserStore from './stores/UserStore.js'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      // meta: { requiresAuth: false } // not being used anymore, using beforeEnter instead
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
      // meta: { requiresAuth: false }
    },
    {
      path: '/sign-up',
      name: 'signUp',
      component: SignUp,
      // meta: { requiresAuth: false }
    },
    {
      path: '/sign-in',
      name: 'signIn',
      component: SignIn,
      // meta: { requiresAuth: false }
    },
    {
      path: '/topics',
      name: 'topics',
      component: Topics,
      beforeEnter: checkRoleUser,
      // meta: { requiresAuth: true }
    },
    {
      path: '/topics/create-topic',
      name: 'createTopic',
      component: CreateTopic,
      beforeEnter: checkRoleUser,
      // meta: { requiresAuth: true }
    },
    {
      path: '/topics/:topicId',
      name: 'topicDetail',
      component: TopicDetail,
      /*param changes will not trigger the beforeEach guard*/
      beforeEnter: checkRoleUser,
      // meta: { requireAuth: true }
    },
    {
      path: '/topics/:topicId/update-topic',
      name: 'updateTopic',
      component: UpdateTopic,
      beforeEnter: checkRoleUser,
      // meta: { requireAuth: true }
    },
    {
      path: '/topics/:topicId/courses/create-course',
      name: 'createCourse',
      component: CreateCourse,
      beforeEnter: checkRoleUser,
      // meta: { requireAuth: true }
    },
    {
      path: '/topics/:topicId/courses/:courseId',
      name: 'courseDetail',
      component: CourseDetail,
      beforeEnter: checkRoleUser,
      // meta: { requireAuth: true }
    },
    {
      path: '/topics/:topicId/courses/:courseId/update-course',
      name: 'updateCourse',
      component: UpdateCourse,
      beforeEnter: checkRoleUser,
      // meta: { requireAuth: true }
    }
  ]
});

function checkRoleUser(to, from, next) {
  const roles = UserStore.data.roles;

  if (!roles) {
    next('/sign-in');
  } else if (roles.includes('ROLE_USER') || roles.includes('ROLE_ADMIN')) {
    next();
  } else {
    next('/sign-in');
  }
}

// function checkRoleAdmin(to, from, next) {
//   UserStore.data.roles.includes('ROLE_ADMIN') ? next() : next('/sign-in');
// }

/*param changes will not trigger the beforeEach guard*/
