import jQuery from 'jquery'
import TopicStore from './TopicStore.js'
import UserStore from './UserStore.js'

const CourseStore = {
  data: {
    currentCourse: null,
    courses: []
  },
  methods: {
    getCourseById(courseId) {
      return jQuery.ajax(`http://localhost:8080/api/courses/${courseId}`, {
        dataType: 'json',
        headers: { Authorization: UserStore.data.accessToken }
      }).done(data => {
        CourseStore.data.currentCourse = data;
      });
    },
    getCoursesByTopicId(topicId) {
      return jQuery.ajax(`http://localhost:8080/api/topics/${topicId}/courses`, {
        dataType: 'json',
        headers: { Authorization: UserStore.data.accessToken }
      }).done(data => {
        CourseStore.data.courses = data;
      });
    },
    createCourse(name, description, topicId) {
      return jQuery.ajax(`http://localhost:8080/api/courses`, {
        contentType: 'application/json',
        data: JSON.stringify({ name, description, topicId }),
        headers: { Authorization: UserStore.data.accessToken },
        type: 'post'
      });
    },
    updateCourse(courseId, name, description, topicId) {
      return jQuery.ajax(`http://localhost:8080/api/courses/${courseId}`, {
        contentType: 'application/json',
        data: JSON.stringify({ name, description, topicId }),
        headers: { Authorization: UserStore.data.accessToken },
        type: 'put'
      }).done(data => {
        CourseStore.data.currentCourse = data;
      });
    },
    deleteCourse(courseId) {
      return jQuery.ajax(`http://localhost:8080/api/courses/${courseId}`, {
        headers: { Authorization: UserStore.data.accessToken },
        type: 'delete'
      });
    },
    validateCourse(name, description, topicId) {
      let errors = [];

      if (!name) {
        errors.push('Course name required');
      } else if (name.length > 25) {
        errors.push('Course name cannot exceed 25 characters');
      } else if (!/\S/.test(name)) {
        errors.push('Course name must contain at least 1 non-whitespace character');
      }

      if (!description) {
				errors.push('Course description required');
      }	else if (description.length > 100) {
				errors.push('Course description cannot exceed 100 characters');
      }	else if (!/\S/.test(description)) {
				errors.push('Course description must contain at least 1 non-whitespace character');
      }

      if (!topicId) {
        errors.push('Topic ID required');
      } else if (!TopicStore.data.topics.find(t => t.id === topicId)) {
        errors.push('Invalid topic ID');
      }
      return errors;
    }
  }
};

export default CourseStore;


