import jQuery from 'jquery'

const UserStore = {
  data: {
    roles: null,
    accessToken: null
  },
  methods: {
    signUp(username, password) {
      return jQuery.ajax(`http://localhost:8080/api/auth/signup`, {
        contentType: 'application/json',
        data: JSON.stringify({ username, password }),
        type: 'post'
      });
    },
    signIn(username, password) {
      return jQuery.ajax('http://localhost:8080/api/auth/signin', {
        contentType: 'application/json',
        data: JSON.stringify({ username, password }),
        type: 'post'
      }).done(data => {
        UserStore.data.roles = data.roles;
        UserStore.data.accessToken = `${data.tokenType} ${data.accessToken}`;
        sessionStorage.setItem('roles', data.roles);
        sessionStorage.setItem('accessToken', UserStore.data.accessToken);
      });
    },
    signOut() {
      sessionStorage.removeItem('roles');
      sessionStorage.removeItem('accessToken');
      UserStore.data.roles = UserStore.data.accessToken = null;
    },
    validateUser(username, password) {
      let errors = [];

      if (!username) {
        errors.push('Username required');
      } else if (username.length < 3 || username.length > 15) {
        errors.push('Username must be 3 to 15 characters long');
      } else if (!/\S/.test(username)) {
        errors.push('Username must contain at least 1 non-whitespace character');
      }

      if (!password) {
        errors.push('Password required');
      }	else if (password.length < 6 || password.length > 20) {
        errors.push('Password must be 6 to 20 characters long');
      }	else if (!/\S/.test(password)) {
        errors.push('Password must contain at least 1 non-whitespace character');
      }
      return errors;
    }
  }
};

export default UserStore;


